@import 'main';
// End Of Import

// Mob Nav
.menuTriger {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba($color-prim, 0.6);
    position: fixed;
    top: 25px;
    right: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 999999;
    display: none;

    @include Sm{
        display: flex;
    }

    i{
        color: #ffffff;
        font-size: 20px;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 9;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 290px;
    height: 100%;
    background-color: #FFF;
    z-index: 99;
    overflow-y: auto;
    transition: .5s;

    &.open{
        right: 0;
    }

    .options {
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #888;
        }

        .close1 {
            display: flex;
            font-size: 20px;
            color: #dedede;
            cursor: pointer;
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        height: 100%;
        width: 290px;
        // overflow: auto;
        padding: 0px 25px 0;

        & > li{
            position: relative;
            padding: 7px 15px;
            width: 100%;

            &:not(:last-child) {
                border-bottom: 1px solid #dedede;
            }

            a {
                width: 100%;
                display: block;
                font-size: 16px;
                color: $color-prim;
                transition: .5s;

                &:hover{
                    color: $color-sec;
                }
            }

            i{
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                &.open{
                    transform: rotate(0deg);
                }
            }

            .drop{
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li{
                    padding: 4px 0;

                    a{
                        font-size: 15px;
                        color: #898989;
                        transition: .5s;
                        
                        &:hover{
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Mob Nav

.up{
    background-color: $color-prim;

    @include Sm{
        display: none;
    }

    .details{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px 0;
    }

    .right{

        span{
            color: $color-white;
            font-size: 12px;

            &:not(:last-child){
                margin-left: 40px;
            }

            i{
                font-size: 12px;
                margin-left: 5px;
            }
        }
    }

    .lang{

        a{
            font-size: 12px;
            color: $color-white;
        }
    }
}

// Head

.head{

    .container{
        border-bottom: 1px solid #f2f2f2;
    }

    .content{
        padding: 10px 0;
        display: flex;
        align-items: center;

        @include Sm{
            flex-direction: column;
        }
    }

    .logo{


        .lg-logo{
            @include Sm{
                display: none;
            }
        }

        .mob-logo{

            @include Md{
                display: none;
            }

            @include Sm{
                display: block;
                width: 200px;
            }
        }
    }

    .search{
        position: relative;
        width: max-content;
        margin: 0 65px;
        margin-top: 25px;

        @include Sm{
            order: 3;
        }

        @include Xs{
            margin-top: 10px;
        }

        @include mob480{
            width: 100%;
        }

        input{
            width: 555px;
            height: 55px;
            border-radius: 100px;
            border: 2px solid #f7f7f7;
            padding: 0 35px;
            color: #b6b6b6;
            transition: .5s;

            &::placeholder{
                color: #b6b6b6;
            }

            &:focus{
                border: 2px solid $color-sec;
            }

            @include Md{
                width: 355px;
            }

            @include Lg{
                width: 555px;
            }

            @include Xs{
                width: 435px;
            }

            @include mob480{
                width: 100%;
            }
        }

        button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 10px;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: $color-sec;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .5s;

            &:hover{
                background-color: $color-prim;
            }
        }
    }

    .menu{
        margin-top: 25px;
        display: flex;
        justify-content: center;
        align-items: center;

        & > div{

            &:hover{
                svg{

                    path{
                        fill: $color-sec;
                    }
                }
            }

            svg{

                path{
                    transition: .5s;
                    fill: $color-prim;
                }
            }

            &:not(:last-child){

                svg{
                    margin-left: 25px;
                }
            }

            &.user{
                position: relative;

                &:hover{
                    
                    .drop{
                        visibility: visible;
                        opacity: 1;
                        transform: translateY(0px);
                    }
                }

                .drop{
                    position: absolute;
                    top: 100%;
                    right: -101%;
                    width: 132px;
                    min-height: 75px;
                    border-radius: 7px;
                    transform: translateY(10px);
                    background-color: rgb(255, 255, 255);
                    text-align: center;                    
                    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
                    opacity: 0;
                    visibility: hidden;
                    z-index: 9;
                    transition: .5s;

                    &:after{
                        content: "";
                        width: 0;
                        height: 0;
                        display: block;
                        border-width: 14px;
                        border-color: transparent transparent #ffffff;
                        border-style: solid;
                        top: -19px;
                        position: absolute;
                        right: 50%;
                        transform: translateX(50%);
                    }

                    ul{
                        padding: 10px 13px;

                        li{
                            padding: 0 12px;

                            a{
                                color: $color-prim;
                                transition: .5s;
                                font-size: 14px;
                                line-height: 2;

                                &:hover{
                                    color: $color-sec;
                                }
                            }

                            &:not(:last-child){
                                border-bottom: 1px solid #f2f2f2;
                            }

                            a{
                                transition: .5s;
                            }
                        }
                    }
                }
            }

            &.cart{
                position: relative;

                span{
                    position: absolute;
                    top: 50%;
                    right: 50%;
                    transform: translate(45%, -35%);
                    color: $color-prim;
                }
            }
        }
    }
}

// Head

// Navbar

.navBar{

    @include Sm{
        display: none;
    }

    & > ul{
        display: flex;
        align-items: center;
        justify-content: center;

        & > li{
            position: relative;

            i{
                font-size: 8px;
                margin-right: 5px;
            }

            &:not(:last-child){
                margin-left: 25px;
            }

            &:hover{
                .drop{
                    visibility: visible;
                    opacity: 1;
                    transform: translateY(0px) translateX(50%);
                }
            }

            .drop{
                position: absolute;
                top: 85%;
                right: 40%;
                width: 132px;
                min-height: 75px;
                border-radius: 7px;
                transform: translateY(10px) translateX(40%);
                background-color: rgb(255, 255, 255);
                text-align: center;
                box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
                opacity: 0;
                visibility: hidden;
                z-index: 9;
                transition: .5s;

                &:after{
                    content: "";
                    width: 0;
                    height: 0;
                    display: block;
                    border-width: 14px;
                    border-color: transparent transparent #ffffff;
                    border-style: solid;
                    top: -19px;
                    position: absolute;
                    right: 50%;
                    transform: translateX(50%);
                }

                ul{
                    padding: 10px 13px;

                    li{
                        padding: 0 12px;

                        a{
                            color: $color-prim;
                            transition: .5s;
                            font-size: 14px;
                            line-height: 2;

                            &:hover{
                                color: $color-sec;
                            }
                        }

                        &:not(:last-child){
                            border-bottom: 1px solid #f2f2f2;
                        }

                        a{
                            transition: .5s;
                        }
                    }
                }
            }

            a{
                font-size: 16px;
                line-height: 2.752;
                color: $color-prim;
                transition: .5s;
                
                &:hover{
                    color: $color-sec;
                }
            }
        }
    }
}

// Navbar

// Main Slider

.main-slider{


    .owl-nav{
        position: absolute;
        top: 49%;
        transform: translate(50%,-50%);
        right: 50%;
        width: 100%;
        height: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 100px;

        @include Sm{
            display: none;
        }

        button{

            &.owl-prev{
                span{
                    border-left: 0;
                    border-bottom: 0;
                }
            }

            &.owl-next{
                span{
                    border-right: 0;
                    border-top: 0;
                }
            }

            span{
                display: block;
                width: 15px;
                height: 15px;
                border: 1px solid #aaaaaa;
                transform: rotate(45deg);
            }
        }
    }

    .owl-dots{
        position: absolute;
        bottom: 40px;
        right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;

        @include mob600{
            bottom: 10px;
        }
        
        button{

            &:not(:last-child){
                margin-left: 5px;
            }

            &.active{

                span{
                    background-color: $color-sec;
                }
            }

            span{
                display: block;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: #707070;
            }
        }
    }
}

// Main Slider

// Start categ
.sections{
    margin: 20px 0 10px;

    .cat{
        position: relative;
        overflow: hidden;

        &:hover{
            & img{
                transform: scale(1.04);
            }
        }

        & > img{
            width: 100%;
            transition: .5s;
            backface-visibility: hidden;
        }

        @include Sm{
            width: max-content;
            margin: auto;
            margin-bottom: 25px;
        }

        @include mob375{
            width: initial;
        }

        .details{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-end;
            color: $color-white;
            position: absolute;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(to bottom, transparent 38%,rgba($color-sec, 0.95) 100%);
            .title{
                font-size: 24px;
                font-family: "frtBold";
            }
            .shop{
                position: relative;
                font-size: 14px;
                font-family: "frtLight";
                color: $color-white;
                margin-bottom: 40px;

                &:after{
                    content: "";
                    position: absolute;
                    bottom: -10px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 50%;
                    height: 1px;
                    background-color: $color-white;
                    transition: .5s;
                }

                &:hover{

                    &:after{
                        width: 100%;
                    }
                }
            }
        }
    }
}
// Sections

// Products

.products{
    padding: 50px 0;

    @include Sm{
        padding: 0px 0 20px;
    }

    h2{
        margin-bottom: 30px;
        font-size: 22px;
        line-height: 2.002;
        font-family: "frtBold";
        color: $color-prim;
    }

    .content{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;

        .Btn{
            width: 100%;
            display: flex;
            justify-content: center;
        }

        .more{
            width: 170px;
            height: 45px;
            border: 2px solid #eaeaea;
            border-radius: 100px;
            font-size: 15px;
            color: rgb(136, 136, 136);
            line-height: 2.936;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .5s;
            
            &:hover{
                border: 2px solid $color-sec;
                background-color: $color-sec;
                color: $color-white;
            }
        }
    }

    .product{
        position: relative;
        text-align: center;
        border-radius: 5px;
        transition: .5s;
        margin-left: 17px;
        width: 214px;
        height: 289px;
        overflow: hidden;
        transition: .5s;
        margin-bottom: 30px;

        &:hover{
            .over{

                button:first-child, button:last-child{
                    opacity: 1;
                    transform: translateY(0);
                    visibility: visible;
                }
            }
        }

        .over{
            position: absolute;
            top: 40%;
            transform: translateY(-50%);
            right: 0;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            button{
                width: 45px;
                height: 45px;
                border-radius: 50%;
                border: 0;
                background-color: rgba($color-prim, 0.85);
                font-size: 18px;
                color: $color-white;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: .5s;
                opacity: 0;
                visibility: hidden;

                &:not(:last-child){
                    margin-left: 5px;
                }

                &:hover{
                    background-color: rgba($color-sec, 0.95);
                }

                &:first-child{
                    transform: translateY(10px);
                }

                &:last-child{
                    transform: translateY(-10px);
                }
            }
        }

        &:hover{
            box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
        }

        @include Md{
            &:nth-of-type(4n){
                margin-left: 0;
            }
        }

        @include Lg{
            &:nth-of-type(5n){
                margin-left: 0;
            }

            &:nth-of-type(4n){
                margin-left: 17px;
            }
        }

        @include Sm{
            &:nth-of-type(3n){
                margin-left: 0;
            }
        }

        @include Xs{
            &:nth-of-type(2n){
                margin-left: 17px;
            }

            // &:nth-of-type(3n){
            //     margin-left: 0;
            // }
        }

        @include mob600{
            &:nth-of-type(2n){
                margin-left: 0;
            }

            &:nth-of-type(3n){
                margin-left: 17px;
            }
            width: 208px;
        }

        @include mob445{
            margin-left: 0;
        }

        .pic{
            height: 208px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #FFF;
        }

        .details{
            margin-top: -25px;

            a{
                display: block;
                text-align: center;
                font-size: 14px;
                font-family: "frtBold";
                color: $color-prim;
                margin-top: 20px;
                margin-bottom: 10px;
                transition: .5s;
            }

            span{
                font-size: 16px;
                font-family: "frtBold";
                display: inline-block;

                &.price{
                    color: $color-sec;
                    margin-right: 8px;
                }

                &.disc{
                    color: #bbbbbb;
                    text-decoration: line-through;
                    font-family: "frt";
                }
            }
        }
    }
}

//Products

// Banner

.banner{
    margin-top: 10px;

    .pic{
        overflow: hidden;

        &:hover{

            img{
                transform: scale(1.02);
            }
        }

        img{
            transition: .5s;
        }
    }
}

// Banner

// Clients

.clients{
    margin-top: 45px;
    margin-bottom: 55px;

    @include Sm{
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .client{
        display: flex;
        justify-content: center;
        align-items: center;

        a{
            width: 132px;
            height: 132px;
            display: flex;
            justify-content: center;
            align-items: center;

            &:hover{
                img{
                    filter: unset;
                    opacity: 1;
                }
            }

            img{
                width: initial;
                display: block;
                margin: auto;
                filter: grayscale(100%);
                opacity: .5;
                transition: .5s;
            }
        }
    }

    .owl-nav{
        position: absolute;
        top: 45%;
        transform: translate(50%,-50%);
        right: 50%;
        width: 105%;
        height: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include Md{
            width: 100%;
        }

        @include Lg{
            width: 105%;
        }

        @include Sm{
            display: none;
        }

        button{

            &.owl-prev{
                span{
                    border-left: 0;
                    border-bottom: 0;
                }
            }

            &.owl-next{
                span{
                    border-right: 0;
                    border-top: 0;
                }
            }

            span{
                display: block;
                width: 15px;
                height: 15px;
                border: 1px solid #d2d2d2;
                transform: rotate(45deg);
                transition: .5s;
            }

            &:hover{

                &.owl-prev{
                    span{
                        border: 1px solid $color-sec;
                        border-left: 0;
                        border-bottom: 0;
                    }
                }

                &.owl-next{
                    span{
                        border: 1px solid $color-sec;
                        border-right: 0;
                        border-top: 0;
                    }
                }
            }
        }
    }

    .owl-dots{
        display: none;
        position: absolute;
        bottom: 0px;
        right: 0;
        justify-content: center;
        align-items: center;
        width: 100%;

        @include Sm{
            display: flex;
        }
        
        button{

            &:not(:last-child){
                margin-left: 5px;
            }

            &.active{

                span{
                    background-color: $color-sec;
                }
            }

            span{
                display: block;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: #707070;
            }
        }
    }
}

// Clients

// News

.news{
    padding: 35px 0;
    background-color: $color-prim;

    .row{
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        @include Md{
            flex-direction: column;
        }

        @include Lg{
            flex-direction: row;
        }

        @include Sm{
            flex-direction: column;
        }
        
        .content{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-basis: calc((100% / 12) * 4);

            @include Sm{
                justify-content: center;
                align-items: center;
            }

            @include Xs{
                flex-direction: column;
            }

            .icon{
                margin-left: 20px;
                i{
                    font-size: 47px;
                    color: rgb(255, 255, 255);
                    line-height: 0.778;                      
                }

                @include Xs{
                    margin: 0;
                    margin-bottom: 15px;
                }
            }

            .details{

                @include Xs{
                    text-align: center;
                }

                p{
                    font-size: 22px;
                    color: rgb(255, 255, 255);
                    line-height: 1.364;
                    font-family: "frtBlk";
                    margin-bottom: 5px;
                }

                span{
                    font-size: 16px;
                    color: rgb(255, 255, 255);
                    line-height: 1.364;
                }
            }
        }

        .form{
            position: relative;
            flex-basis: calc((100% / 12) * 5);

            @include Md{
                margin: 25px 0;
            }

            @include Lg{
                margin: 0;
            }

            @include Sm{
                margin: 25px 0;
            }

            @include mob480{
                width: 90%;
            }

            input{
                background-color: rgb(255, 255, 255);
                width: 458px;
                height: 55px;
                border: 0;
                border-radius: 100px;
                padding: 0 40px;
                font-size: 15px;
                color: rgb(159, 158, 158);
                line-height: 2;
                
                &::placeholder{
                    color: rgb(159, 158, 158);
                }

                @include mob480{
                    width: 100%;
                }
            }

            button{
                width: 40px;
                height: 40px;
                border: 0;
                background-color: transparent;
                font-size: 18px;
                color: #531958;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: .5s;

                @include Md{
                    left: 30px;
                }

                @include Lg{
                    left: 50px;
                }

                @include Sm{
                    left: 20px;
                }

                &:hover{
                    color: $color-sec;
                }
            }
        }

        .social{
            display: flex;
            align-items: center;
            flex-basis: calc((100% / 12) * 3);

            a{
                display: block;
                width: 45px;
                height: 45px;
                background-color: #702f76;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: $color-white;
                font-size: 18px;
                transition: .5s;

                &:not(:last-child){
                    margin-left: 10px;
                }

                &:hover{
                    background-color: #b46b93;
                }
            }
        }
    }
}

// News

// footer

footer{
    padding: 58px 0;
    padding-bottom: 0;

    @include Sm{
        padding: 40px 0;
    }

    .foot-links{

        @include Md{
            text-align: center;
        }

        @include Lg{
            text-align: right;
        }

        @include Sm{
            margin-bottom: 15px;
        }

        .collapse{
            display: block;

            @include Sm{
                display: none;
            }

            &.in{
                display: block;
            }
        }

        & > button{
            font-size: 16px;
            font-family: "frtBold";
            color: #595959;
            margin-bottom: 20px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans{

                &:after{
                    content: "\2212";
                }
            }

            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }

        ul{

            @include Sm{
                padding-top: 10px;
                padding-right: 10px;
            }

            li{
                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }
        }

        a{
            font-size: 14px;
            font-family: "frt";
            color: #898989;
            transition: .5s;

            &:hover{
                color: $color-prim;
            }
        }
    }

    .download{
        
        @include Md{
            margin-top: 35px;
            text-align: center;
        }

        @include Lg{
            margin-top: 0px;
            text-align: right;

            .pics{
                display: flex;
                justify-content: flex-start;
            }
        }

        @include Sm{
            margin-top: 10px;
            text-align: center;

            .pics{
                display: flex;
                justify-content: center;
            }
        }

        & > p{
            font-size: 18px;
            color: rgb(83, 25, 88);
            line-height: 1.667;
            font-family: "frtBold";
            margin-bottom: 30px;

            @include Sm{
                margin-bottom: 15px;
            }
        }

        .pics{
            display: flex;
            align-items: center;

            @include Md{
                display: flex;
                justify-content: center;
            }

            @include Lg{
                display: flex;
                justify-content: flex-start;
            }

            a{
                &:first-child{
                    margin-left: 5px;
                }
            }
        }
    }

    .pay{
        margin-top: 35px;
        margin-bottom: 50px;
        display: block;

        @include Sm{
            margin-top: 20px;
            margin-bottom: 0px;
        }

        img{
            margin: auto;
        }
    }
}

// End Of Footer

// End

.end{

    .container{
        border-top: 1px solid #ededed;
    }

    .content{
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include mob600{
            flex-direction: column;
        }

        @include Xs{
            text-align: center;
        }


        p,a{
            font-size: 14px;
            color: rgb(97, 97, 97);
            line-height: 3.146;
            margin-bottom: 0;

            @include Xs{
                line-height: 3;
                font-size: 13px;
            }
        }

        a{

            img{
                margin-right: 5px;
            }
        }
    }
}

